import Link from "next/link";
import { connect } from "react-redux";
import setContext from "../store.js";
function Context({ setContext }) {
  return (
    <div>
      <Link href="/session-setup">
        <p onClick={() => setContext()}>choose ind</p>
      </Link>
      <Link href="/session-setup">
        <p>choose neth</p>
      </Link>
    </div>
  );
}
function mapDispatchToProps(dispatch) {
  return { setContext: dispatch(setContext("netherlands")) };
}

export default connect(mapDispatchToProps)(Context);



